    //‰∏ãËΩΩGif
    downloadGif = function()
    {
        var gif = new GIF({
            workers: 2,
            quality: 10,
            width: 460, 
            height: 460
          });
        
        $('.gifList li img').each((index,item)=>
        {
            gif.addFrame( item,{ delay: 1000 }); 
        });

        gif.on('finished', function(blob) 
        {
            download(blob, "test.gif", "image/gif") ;
        });

        gif.render();

    }
    

    
